.mantine-InputWrapper-label {
  text-wrap: nowrap;
}

.container {
  display: flex;
  flex-direction: column;
  gap: 8px;

  padding: 32px;

  border: 1px solid #333;

  resize: horizontal;
  overflow: auto;
}

.row-grid {
  display: grid;
  column-gap: 8px;
  row-gap: 8px;
}

.grid-cols-4 {
  grid-template-columns: repeat(4, 1fr);
}

.grid-cols-5 {
  grid-template-columns: repeat(5, 1fr);
}

.row-grid .weight-1 {
  grid-column-start: span 1;
  min-width: 10px;
}

.row-grid .weight-2 {
  grid-column-start: span 2;
  min-width: 10px;
}

.row-grid .weight-3 {
  grid-column-start: span 3;
  min-width: 10px;
}

.row-grid .weight-4 {
  grid-column-start: span 4;
  min-width: 10px;
}

.row-grid .weight-5 {
  grid-column-start: span 5;
  min-width: 10px;
}

.weights-list {
  padding: 2px;
  border-radius: 4px;
  background-color: #22242c;
}

.weight-label {
  width: 24px;
  text-align: center;
  user-select: none;
}
